:root {
  --primary-color: #1E3A8A; /* Original background color */
  --secondary-color: #F59E0B;
  --background-gradient: radial-gradient(circle, #1e3a8a 0%, #111827 100%);
  --text-color: #FFFFFF;
  --input-bg: rgba(255, 255, 255, 0.15);
  --input-border: rgba(255, 255, 255, 0.3);
  --success-bg: rgba(46, 204, 113, 0.15);
  --error-bg: rgba(231, 76, 60, 0.15);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh; /* Ensures form is vertically centered */
  background: var(--background-gradient);
  font-family: 'Inter', sans-serif;
  padding: 20px;
  box-sizing: border-box;
  overflow: hidden;
}

.overlay {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(12px);
  border: 1px solid var(--input-border);
  width: 100%;
  max-width: 40%; /* Prevents form from exceeding the viewport width */
  padding: 20px;
  color: var(--text-color);
  animation: fadeIn 0.8s ease-out;
  box-sizing: border-box;
  max-height: 90vh; /* Limits the form height to avoid overflow */
  overflow-y: auto; /* Adds scroll if content exceeds the viewport height */
}

/* Fade-in Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.title {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 800;
  color: var(--secondary-color);
  margin-bottom: 20px;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  position: relative;
}

.title::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, #14B8A6, var(--secondary-color));
  border-radius: 2px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
}

.inputField,
.selectField,
.textareaField {
  background: var(--input-bg);
  border: 1px solid var(--input-border);
  border-radius: 10px;
  padding: 12px;
  font-size: 1rem;
  color: var(--text-color);
  transition: all 0.3s ease;
  box-sizing: border-box;
  width: 100%; /* Ensure inputs do not exceed the form's width */
}

.inputField::placeholder,
.textareaField::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.inputField:focus,
.selectField:focus,
.textareaField:focus {
  border-color: var(--secondary-color);
  box-shadow: 0 0 6px rgba(245, 158, 11, 0.5);
  background-color: rgba(0, 0, 0, 0.8);
}

.textareaField {
  resize: vertical;
  min-height: 80px;
  max-height: 150px;
}

.submitBtn {
  background: linear-gradient(45deg, #14B8A6, var(--secondary-color));
  border: none;
  color: var(--text-color);
  font-size: 1rem;
  font-weight: 700;
  padding: 12px;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-transform: uppercase;
}

.submitBtn:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 30px rgba(245, 158, 11, 0.5);
}

.successMessage,
.errorMessage {
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  padding: 10px;
  border-radius: 8px;
}

.successMessage {
  background: var(--success-bg);
  color: #2ECC71;
  border: 1px solid rgba(46, 204, 113, 0.4);
}

.errorMessage {
  background: var(--error-bg);
  color: #E74C3C;
  border: 1px solid rgba(231, 76, 60, 0.4);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .overlay {
    max-width: 80%;
    padding: 20px;
  }

  .title {
    font-size: 1.6rem;
  }

  .inputField,
  .selectField,
  .textareaField {
    font-size: 0.95rem;
    padding: 10px 12px;
  }

  .submitBtn {
    font-size: 0.9rem;
    padding: 10px;
  }
}

@media (max-width: 768px) {
  .overlay {
    max-width: 80%;
    padding: 15px;
  }

  .title {
    font-size: 1.5rem;
  }

  .inputField,
  .selectField,
  .textareaField {
    font-size: 0.85rem;
    padding: 8px;
  }

  .submitBtn {
    font-size: 0.85rem;
    padding: 8px;
  }

  .successMessage,
  .errorMessage {
    font-size: 0.85rem;
    padding: 8px;
  }
}

@media (max-width: 480px) {
  .overlay {
    padding: 10px;
    max-width: 95%; /* Further limits for extra small devices */
  }

  .title {
    font-size: 1.4rem;
  }

  .form {
    gap: 12px;
  }

  .inputField,
  .selectField,
  .textareaField {
    font-size: 0.8rem;
    padding: 6px;
  }

  .submitBtn {
    font-size: 0.8rem;
    padding: 6px;
  }

  .successMessage,
  .errorMessage {
    font-size: 0.8rem;
    padding: 6px;
  }
}
