:root {
  --primary-color: #2B6CB0;
  --secondary-color: #F6AD55;
  --background-color: #EDF2F7;
  --text-color: #2D3748;
  --font-family: 'Inter', Arial, sans-serif;
}

.details-page-container {
  font-family: var(--font-family);
  background: radial-gradient(circle at top, rgba(30, 58, 138, 0.9), rgba(17, 24, 39, 1));
  color: #E2E8F0;
  padding: 60px 20px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.details-layout {
  display: flex;
  flex-direction: column; /* Always use a column layout */
  align-items: center;
  max-width: 800px;
  margin: 0 auto;
  background: #2D3748;
  border-radius: 24px;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  padding: 20px;
  transition: transform 0.3s ease-in-out;
}

.details-layout:hover {
  transform: scale(1.02);
}

.space-image-container {
  width: 100%;
  max-width: 700px;
  margin-bottom: 30px; /* Ensure spacing for stacked layout */
}

.space-image {
  width: 100%;
  height: auto;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.space-image:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.5);
}

.space-details-container {
  width: 100%;
  max-width: 700px;
  text-align: center;
  padding: 20px;
}

.space-details-container h1 {
  font-size: 2.5rem;
  color: #F59E0B;
  margin-bottom: 20px;
  font-weight: 700;
  text-align: center;
}

.space-details-container p {
  font-size: 1.1rem;
  line-height: 1.8;
  margin-bottom: 15px;
  font-weight: 500;
}

.space-details-container .space-price {
  font-size: 1.8rem;
  color: #F6AD55;
  font-weight: bold;
  margin-top: 25px;
}

.configure-button {
  background: linear-gradient(45deg, #14B8A6, #FBBF24);
  color: white;
  padding: 14px 50px;
  border: none;
  cursor: pointer;
  margin-top: 30px;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
}

.configure-button:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(20, 184, 166, 0.5);
}

/* Loading and Error Messages */
.loading-message,
.error-message {
  text-align: center;
  color: var(--text-color);
  font-size: 1.3rem;
  font-weight: 600;
  padding: 25px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  background: rgba(246, 173, 85, 0.3);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

/* Responsive Design */
@media (min-width: 768px) {
  .details-layout {
    padding: 40px 20px;
  }
}

@media (min-width: 1200px) {
  .details-layout {
    padding: 60px 30px;
  }

  .space-image-container {
    max-width: 800px;
  }

  .space-details-container {
    max-width: 800px;
  }

  .space-details-container h1 {
    font-size: 3rem;
  }

  .configure-button {
    font-size: 1.3rem;
    padding: 16px 60px;
  }
}
