:root {
  --primary-color: #2B6CB0;
  --secondary-color: #F59E0B;
  --background-color: rgba(30, 58, 138, 0.8);
  --text-color: #F9FAFB;
  --input-bg: rgba(255, 255, 255, 0.1);
  --input-border: #374151;
  --success-bg: rgba(20, 184, 166, 0.2);
  --error-bg: rgba(192, 57, 43, 0.2);
}

.mainContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  background: radial-gradient(circle, var(--background-color), rgba(17, 24, 39, 1));
  width: 100%;
  min-height: 100vh;
  padding: 20px;
  color: var(--text-color);
}

/* Form Wrapper */
.formWrapper {
  background: rgba(255, 255, 255, 0.08);
  padding: 30px 40px;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(30, 58, 138, 0.5);
  border: 1px solid rgba(30, 58, 138, 0.2);
  width: 100%;
  max-width: 450px;
  animation: fadeIn 1s ease-out;
}

/* Fade-In Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Title */
.title {
  text-align: center;
  margin-bottom: 30px;
  color: var(--secondary-color);
  font-weight: 800;
  font-size: 2rem;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  position: relative;
}

.title::after {
  content: '';
  position: absolute;
  width: 80px;
  height: 3px;
  background: linear-gradient(to right, var(--secondary-color), #fbbf24);
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 2px;
}

/* Form */
.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Input Group */
.inputGroup {
  position: relative;
  width: 100%;
}

/* Input Icons */
.inputIcon {
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
  color: var(--secondary-color);
  font-size: 1.2rem;
}

/* Input Fields */
.inputField,
.selectField,
.textareaField {
  width: 100%;
  padding: 14px 14px 14px 45px;
  background: var(--input-bg);
  border: 1px solid var(--input-border);
  border-radius: 12px;
  color: var(--text-color);
  font-size: 1rem;
  outline: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.inputField:focus,
.selectField:focus,
.textareaField:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 6px rgba(59, 130, 246, 0.6);
  background-color: #1e293b;
}

/* Textarea Field */
.textareaField {
  resize: vertical;
  min-height: 100px;
}

/* Floating Labels */
.label {
  position: absolute;
  top: 50%;
  left: 45px;
  transform: translateY(-50%);
  background: transparent;
  padding: 0 5px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.95rem;
  pointer-events: none;
  transition: all 0.3s ease;
}

.inputField:focus + .label,
.inputField:not(:placeholder-shown) + .label,
.selectField:focus + .label,
.selectField:not(:placeholder-shown) + .label,
.textareaField:focus + .label,
.textareaField:not(:placeholder-shown) + .label {
  top: -8px;
  left: 40px;
  font-size: 0.85rem;
  color: #94a3b8;
}

/* Submit Button */
.submitBtn {
  background: linear-gradient(45deg, #14B8A6, #FBBF24);
  color: #FFFFFF;
  border: none;
  border-radius: 12px;
  padding: 14px;
  font-size: 1.1rem;
  font-weight: 700;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.submitBtn:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 25px rgba(20, 184, 166, 0.5);
}

/* Success and Error Messages */
.successMessage,
.errorMessage {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
  border-radius: 10px;
  font-size: 1rem;
}

.successMessage {
  background: var(--success-bg);
  color: #14B8A6;
}

.errorMessage {
  background: var(--error-bg);
  color: #C0392B;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .formWrapper {
    padding: 25px 30px;
  }

  .title {
    font-size: 1.8rem;
  }

  .submitBtn {
    font-size: 1rem;
    padding: 12px;
  }
}
