:root {
  --primary-color: rgba(30, 58, 138, 0.8); /* Original background color */
  --secondary-color: #F59E0B;
  --background-color: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  --text-color: #FFFFFF;
  --input-bg: rgba(255, 255, 255, 0.1);
  --input-border: rgba(255, 255, 255, 0.3);
  --success-bg: rgba(46, 204, 113, 0.15);
  --error-bg: rgba(231, 76, 60, 0.15);
}

.deleteCwsContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  font-family: 'Inter', sans-serif;
  background: var(--background-color);
  box-sizing: border-box;
  padding: 20px;
}

/* Overlay */
.deleteCwsOverlay {
  background: var(--input-bg);
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 500px; /* Adjusted for responsiveness */
  padding: 30px;
  box-sizing: border-box;
  animation: fadeIn 0.8s ease-out;
  overflow: hidden;
  position: relative;
}

/* Title */
.deleteCwsTitle {
  color: var(--secondary-color);
  font-size: 2rem;
  font-weight: 800;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 25px;
  position: relative;
}

.deleteCwsTitle::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, #14b8a6, #3b82f6);
  border-radius: 2px;
}

/* Form Container */
.formContainer {
  width: 100%;
  background: var(--input-bg);
  border: 1px solid var(--input-border);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

/* Form Label */
.formLabel {
  color: var(--text-color);
  font-size: 0.95rem;
  font-weight: 500;
  margin-bottom: 10px;
}

/* Form Field */
.formField {
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  border: 1px solid var(--input-border);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.15);
  color: var(--text-color);
  transition: all 0.3s ease;
}

.formField::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.formField:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 6px rgba(59, 130, 246, 0.5);
  background-color: rgba(0, 0, 0, 0.9);
}

/* Delete Button */
.deleteSpaceBtn {
  padding: 12px;
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-color);
  background: linear-gradient(90deg, #c0392b, #e74c3c);
  border: none;
  border-radius: 10px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.deleteSpaceBtn:hover {
  background: linear-gradient(90deg, #e74c3c, #c0392b);
  transform: scale(1.05);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
}

/* Success and Error Messages */
.successMessage,
.errorMessage {
  font-size: 0.9rem;
  font-weight: 500;
  text-align: center;
  margin-top: 10px;
  padding: 10px;
  border-radius: 8px;
}

.successMessage {
  background: var(--success-bg);
  color: #2ecc71;
  border: 1px solid rgba(46, 204, 113, 0.4);
}

.errorMessage {
  background: var(--error-bg);
  color: #e74c3c;
  border: 1px solid rgba(231, 76, 60, 0.4);
}

/* Fade-in Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .deleteCwsOverlay {
    max-width: 90%; /* Reduce overlay size */
    padding: 25px;
  }

  .deleteCwsTitle {
    font-size: 1.8rem;
  }

  .formContainer {
    gap: 15px;
  }

  .formField {
    font-size: 0.95rem;
    padding: 10px;
  }

  .deleteSpaceBtn {
    font-size: 0.95rem;
    padding: 10px;
  }
}

@media (max-width: 768px) {
  .deleteCwsOverlay {
    max-width: 95%;
    padding: 20px;
  }

  .deleteCwsTitle {
    font-size: 1.6rem;
  }

  .formContainer {
    gap: 10px;
  }

  .formField {
    font-size: 0.85rem;
    padding: 8px;
  }

  .deleteSpaceBtn {
    font-size: 0.85rem;
    padding: 8px;
  }

  .successMessage,
  .errorMessage {
    font-size: 0.85rem;
    padding: 8px;
  }
}

@media (max-width: 480px) {
  .deleteCwsOverlay {
    max-width: 95%;
    padding: 15px;
  }

  .deleteCwsTitle {
    font-size: 1.4rem;
  }

  .formContainer {
    gap: 8px;
  }

  .formField {
    font-size: 0.8rem;
    padding: 6px;
  }

  .deleteSpaceBtn {
    font-size: 0.8rem;
    padding: 6px;
  }

  .successMessage,
  .errorMessage {
    font-size: 0.8rem;
    padding: 6px;
  }
}
