* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --sidebar-bg: rgba(30, 60, 114, 0.7);
  --sidebar-bg-blur: blur(15px);
  --sidebar-border: rgba(255, 255, 255, 0.2);
  --button-hover-bg: rgba(36, 204, 44, 0.2);
  --button-active-bg: rgba(36, 204, 44, 0.4);
  --text-color-side-bar: #ffffff;
  --text-hover-color: #24cc2c;
  --font-family: 'Poppins', sans-serif;
  --transition-speed: 0.3s;
  --shadow-color: rgba(0, 0, 0, 0.2);
  --button-shadow: 0 5px 10px var(--shadow-color), inset 0 2px 4px var(--shadow-color);
  --button-active-shadow: inset 0 4px 8px rgba(0, 0, 0, 0.3);
}

html,
body {
  height: 100%;
  font-family: var(--font-family);
}

.main-container {
  display: flex;
  min-height: 100vh;
}

/* Sidebar Styling */
.sidebar {
  background: var(--sidebar-bg);
  backdrop-filter: var(--sidebar-bg-blur); /* Frosted glass effect */
  border-right: 1px solid var(--sidebar-border);
  width: 300px; 
  min-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 40px 30px; 
  transition: transform var(--transition-speed) ease-in-out,
    box-shadow var(--transition-speed) ease-in-out;
  box-shadow: 2px 0 15px var(--shadow-color);
  position: relative;
  z-index: 1000;
  border-radius: 20px 0 0 20px; /* Smooth edges */
  overflow: hidden;
}

/* Sidebar Title */
.sidebar h1 {
  margin-bottom: 40px;
  font-size: 30px;
  color: #fbbf24;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  position: relative;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.sidebar h1::after {
  content: '';
  position: absolute;
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, #24cc2c, #fbbf24);
  bottom: -12px;
  left: 0;
  border-radius: 2px;
}

/* Navigation List */
.sidebar ul {
  list-style: none;
  width: 100%;
}

/* Navigation Items */
.sidebar ul li {
  margin: 20px 0;
}

/* Navigation Buttons */
.sidebar ul li button {
  width: 100%;
  padding: 15px 25px;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 12px;
  font-weight: 600;
  font-size: 16px;
  color: var(--text-color-side-bar);
  text-align: left;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: all var(--transition-speed) ease;
  cursor: pointer;
  box-shadow: var(--button-shadow);
}

.sidebar ul li button:hover,
.sidebar ul li button:focus {
  background-color: var(--button-hover-bg);
  color: var(--text-hover-color);
  box-shadow: 0 8px 20px rgba(36, 204, 44, 0.4);
  transform: translateX(10px); /* Smooth movement */
}

.sidebar ul li button:focus {
  outline: 3px solid #fbbf24; 
  outline-offset: 3px;
}

.sidebar ul li button.active {
  background-color: var(--button-active-bg);
  color: var(--text-color-side-bar);
  font-weight: bold;
  box-shadow: var(--button-active-shadow);
  transform: scale(1.05);
}

/* Icons Inside Buttons */
.iconButton {
  width: 24px;
  height: 24px;
  color: var(--text-color-side-bar);
  transition: transform var(--transition-speed) ease, color var(--transition-speed) ease;
}

.sidebar ul li button:hover .iconButton,
.sidebar ul li button:focus .iconButton {
  transform: scale(1.2);
  color: var(--button-hover-color);
}

.sidebar ul li button.active .iconButton {
  color: var(--text-color-side-bar);
}

/* Toggle Button (Hamburger/Close Icons) */
.icon {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 28px;
  color: var(--text-color-side-bar);
  transition: transform var(--transition-speed) ease;
  position: fixed;
  top: 25px;
  left: 25px;
  z-index: 1100;
}

.icon:hover {
  transform: rotate(90deg);
}

/* Main Content Area */
.main-content-area {
  flex-grow: 1;
  padding: 40px;
  background-color: #f4f4f4;
  overflow-y: auto;
  transition: margin-left var(--transition-speed) ease;
}

/* Overlay Styling */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 500;
  cursor: pointer;
  transition: background var(--transition-speed) ease;
}

/* Mobile Styles */
@media screen and (max-width: 768px) {
  .main-container {
    flex-direction: column;
  }

  .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 85%; 
    max-width: 320px;
    transform: translateX(-100%);
    z-index: 1000;
    box-shadow: 2px 0 20px rgba(0, 0, 0, 0.4);
  }

  .sidebar.active {
    transform: translateX(0);
  }

  .icon {
    display: block;
  }

  .main-content-area {
    margin-left: 0;
    width: 100%;
    padding: 20px;
  }
}

/* Tablet and Above */
@media screen and (min-width: 769px) {
  .sidebar {
    transform: translateX(0);
    position: relative;
    height: auto;
  }

  .icon {
    display: none;
  }
}

/* Larger Screens */
@media screen and (min-width: 992px) {
  .sidebar h1 {
    font-size: 32px;
  }

  .sidebar ul li button {
    font-size: 18px;
  }

  .iconButton {
    width: 28px;
    height: 28px;
  }
}

@media screen and (min-width: 1400px) {
  .sidebar h1 {
    font-size: 36px;
  }

  .sidebar ul li button {
    font-size: 20px;
  }

  .iconButton {
    width: 32px;
    height: 32px;
  }
}
