:root {
  --primary-color: #2B6CB0;
  --secondary-color: #F6AD55;
  --background-color: #EDF2F7;
  --text-color: #2D3748;
  --font-family: 'Inter', sans-serif;
}

.main-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  width: 100%;
  min-height: 100vh;
  padding: 20px;
  font-family: var(--font-family);
}

.card-container {
  width: 100%;
  max-width: 700px;
  padding: 30px;
  background: rgba(45, 55, 72, 0.9);
  border-radius: 20px;
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #ffffff;
  animation: fadeIn 0.8s ease-out;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card-container:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.6);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.title {
  text-align: center;
  margin-bottom: 25px;
  color: var(--secondary-color);
  font-size: 2.4rem;
  font-weight: 800;
  letter-spacing: 2px;
  text-transform: uppercase;
  position: relative;
}

.title::after {
  content: '';
  position: absolute;
  width: 100px;
  height: 4px;
  background: linear-gradient(to right, #fbbf24, #f59e0b);
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 5px;
}

.reservation-count {
  text-align: center;
  margin-bottom: 20px;
  font-size: 1.2rem;
}

.strong {
  font-weight: bold;
  color: var(--secondary-color);
}

.loader {
  text-align: center;
  font-size: 1.4rem;
  color: #3b82f6;
  margin: 20px 0;
}

.error {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  padding: 15px;
  border-radius: 10px;
  background: rgba(192, 57, 43, 0.2);
  color: #c0392b;
  font-weight: 600;
  font-size: 1rem;
}

.message-icon {
  margin-right: 10px;
  font-size: 1.3rem;
}

.ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.card {
  background: linear-gradient(135deg, rgba(45, 55, 72, 1), rgba(45, 55, 72, 0.9));
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 25px;
  margin-bottom: 20px;
  color: #ffffff;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.3);
}

.card p {
  margin-bottom: 10px;
  font-size: 1rem;
  line-height: 1.6;
}

.card p span {
  font-weight: 600;
  color: var(--secondary-color);
}

.btn-container {
  display: flex;
  justify-content: flex-end;
  padding-top: 15px;
}

.delete-btn {
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: 600;
  background: linear-gradient(90deg, #c0392b, #e74c3c);
  border: none;
  border-radius: 12px;
  color: #ffffff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
}

.delete-btn:hover {
  background: linear-gradient(90deg, #e74c3c, #c0392b);
  transform: scale(1.05);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
}

.delete-icon {
  margin-right: 8px;
  font-size: 1.2rem;
}

.no-reservations {
  text-align: center;
  color: #9ca3af;
  font-size: 1.2rem;
  padding: 25px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
}

@media (max-width: 768px) {
  .card-container {
    max-width: 90%;
    padding: 20px;
  }

  .title {
    font-size: 1.8rem;
  }

  .card {
    padding: 20px;
  }

  .delete-btn {
    padding: 8px 15px;
    font-size: 0.9rem;
  }
}
